import React from 'react';
import { Link, useLocation } from '@reach/router';

const Breadcrumb = () => {
	const { pathname } = useLocation();
	const paths = pathname.split('/')
		.map((x) => x.replace(/\//g, ''))
		.filter((x) => x !== '');
	const breadCrumbs = paths.reduce((acc, cur, i) => {
		if (i === 0) {
			acc.push({ key: cur, value: cur });
		} else {
			acc.push({ key: cur, value: `${acc[i - 1].value}/${cur}` });
		}
		return acc;
	}, []);

	return (
		<div className="vrc__breadcrumbContainer">
			{breadCrumbs.map((x, i) => {
				const p = pathname.replace(/\//g, '');
				const v = x.value.replace(/\//g, '');
				return (
					<>
						{p === v
							? <span className="vrc__breadcrumbCurrent">{x.key}</span>
							: <Link to={x.value}>{x.key}</Link>}
						{i < paths.length - 1 && (
							<span>
								{' '}
								/
								{' '}
							</span>
						)}
					</>
				);
			})}
		</div>
	);
};
export default Breadcrumb;
