import React, { useState, useEffect, useRef } from 'react';
import { Link } from '@reach/router';
import { Navbar, Nav, Container } from 'react-bootstrap';
import useClickOutside from '../utils/useClickOutside';

const Index = () => {
	const [sticky, setSticky] = useState(null);
	const [open, setOpen] = useState(null);
	const navref = useRef(null);
	const toggleref = useRef(null);

	const toggleNavbar = () => {
		if (open === null) {
			return;
		}
		setOpen(!open);
	};

	const closeNavbar = () => {
		if (open === true) {
			setOpen(!open);
		}
	};

	useClickOutside(navref, closeNavbar);

	useEffect(() => {
		const onScroll = () => {
			if (window.scrollY >= 50 && sticky !== true) {
				setSticky(true);
			} else if (window.scrollY <= 10 && sticky !== false) {
				setSticky(false);
			}
		};
		document.addEventListener('scroll', onScroll);
		return function cleanup() {
			window.removeEventListener('scroll', onScroll);
		};
	});

	return (
		<Navbar fixed="top" expanded={open} collapseOnSelect expand="lg" bg="dark" variant="dark" className="vrc__navbar" ref={navref} onToggle={() => setOpen(true)}>
			<Container>
				<Link to="/" className="navbar-brand">VrC</Link>
				<Navbar.Toggle aria-controls="responsive-navbar-nav" onClick={() => setTimeout(toggleNavbar, 0)} ref={toggleref} />
				<Navbar.Collapse id="responsive-navbar-nav">
					<Nav className="mr-auto">
						<Link to="/" className="nav-link" onClick={toggleNavbar}>Home</Link>
						<Link to="blog" className="nav-link" onClick={toggleNavbar}>Blog</Link>
						<Link to="chart" className="nav-link" onClick={toggleNavbar}>Charts</Link>
						<Link to="contact" className="nav-link" onClick={toggleNavbar}>Contact</Link>
					</Nav>
				</Navbar.Collapse>
			</Container>
		</Navbar>
	);
};

export default Index;
