import React from 'react';
import { Jumbotron } from 'react-bootstrap';
import ContainerLayout from '../components/containerLayout';

const Contact = () => (
	<ContainerLayout>
		<br />
		<h1>Contact</h1>
		<br />
		<Jumbotron>
			<h1>Who am I?</h1>
			<p>
				My name is Stefano Vuerich, I'm software developer and a ski instructor.
			</p>
			During my free time I like to run, climb, hike.
			<p />
		</Jumbotron>
	</ContainerLayout>
);

export default Contact;
