const importCharts = (setChartsCallback) => {
    const importAll = (r) => {
        r.keys().forEach(x => {
            var chart = x.substring(2, x.indexOf('.jsx'));

            import(`../pages/chart/${chart}`).then(res => {
                setChartsCallback(res)
            })
        });
    }

    return importAll(require.context('../pages/chart/', true, /\.jsx$/));
}

export default importCharts;