import React, { Component } from 'react';
import VideoLoder from './videoLoader';

const withVideoLoader = (meta, chart) => class extends Component {
	constructor(props) {
		super(props);
	}

	render() {
		return (
			<>
				{/* <h2>{meta.title}</h2>
				<hr /> */}
				<br />
				<VideoLoder
					videoIds={chart}
					onShowLyrics={this.onShowLyrics}
					onCloseLyrics={this.onCloseLyrics}
				/>
			</>
		);
	}
};

export default withVideoLoader;
