import React from 'react';
import { Link } from '@reach/router';
import { useStaticQuery, graphql } from 'gatsby';
import Image from './image';

const BlogList = ({ blogs, elementsPerRow = 3 }) => {
	const data = useStaticQuery(graphql`
					  query {
						images: allFile(filter: { extension: { eq: "jpg" } }) {
						  edges {
							node {
							  relativePath
							  name
							  childImageSharp {
								sizes(maxWidth: 600) {
								  ...GatsbyImageSharpSizes
								}
							  }
							}
						  }
						}
					  }
					  `);
	const reducedBlogs = blogs.reduce((acc, cur) => {
		const index = Object.keys(acc).length || 1;
		if (!acc[index]) {
			acc[index] = [cur];
		} else if (acc[index].length === elementsPerRow) {
			acc[index + 1] = [cur];
		} else {
			acc[index] = acc[index].concat([cur]);
		}
		return acc;
	}, {});
	return (
		<div className="container vrc__blogListContainer">
			{Object.keys(reducedBlogs).map((x, xi) => (
				<div className="row vrc__blogListRow" key={xi}>
					{reducedBlogs[x].map((y, yi) => {
						const image = data.images.edges.find(n => {
							return n.node.relativePath.includes(y._frontmatter.image);
						});
						if (!image) {
							return null;
						}
						return (
							<div className={`col-md-${12 / elementsPerRow} vrc__blogListCol`} key={yi}>
								<div className="vrc__blogListCard">
									<Link to={y._frontmatter.path}>
										<Image
											image={image}
											alt={y._frontmatter.title}
										/>
										<div className="vrc__blogCardBody">
											<h2>{y._frontmatter.title}</h2>
											<span>{y._frontmatter.intro}</span>
										</div>
									</Link>
								</div>
							</div>
						);
					})}
				</div>
			))}
		</div>
	);
};

export default BlogList;
