import React, { Component } from 'react';
import SEO from '../components/seo';
import BlogList from '../components/blogList';
import importBlogs from '../utils/importBlogs';
import ChartList from '../components/chartList';
import importCharts from '../utils/importCharts';
import HomeImg from '../images/home.jpg';

class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			blogs: [],
			charts: [],
		};
	}

	componentDidMount() {
		if (typeof window !== 'undefined') {
			const image = document.getElementById('vrc__homeImg');
			new window.simpleParallax(image, {
				scale: 1.8,
				delay: 0.6,
				transition: 'cubic-bezier(0,0,0,1)',
			});
		}
		importBlogs((blog) => {
			this.setState((state) => ({
				blogs: state.blogs
					.concat([blog])
					.sort((a, b) => new Date(b.meta.date) - new Date(a.meta.date)),
			}));
		});
		importCharts((chart) => {
			this.setState((state) => ({
				charts: state.charts
					.concat([chart])
					.sort((a, b) => new Date(b.meta.date) - new Date(a.meta.date)),
			}));
		});
	}

	render() {
		const { blogs, charts } = this.state;
		return (
			<>
				<SEO title="Home" />
				<div className="vrc__homeImgContainer">
					<img src={HomeImg} id="vrc__homeImg" />
				</div>
				<div className="container">
					<br />
					<h2>Blogs</h2>
					<hr />
					<BlogList blogs={blogs} />
					<h2>Charts</h2>
					<hr />
					<ChartList charts={charts} />
				</div>
			</>
		);
	}
}

export default Home;
